.c-hero-wrapper {
	position: relative;
	background:
		var(--hero-gradient),
		var(--background-image-small) no-repeat center/cover;

	margin-bottom: 6em;
}
@media (min-width: 768px) {
	.c-hero-wrapper {
		background:
				var(--hero-gradient),
				var(--background-image-medium) no-repeat center/cover;
	}
}

@media (min-width: 1024px) {
	.c-hero-wrapper {
		background:
				var(--hero-gradient),
				var(--background-image-large) no-repeat center/cover;
	}
}

.kc-hero {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	color: white;
	font-size: 1.5rem;
	min-height: 25rem;
	z-index: 0;
	max-width: 67.5rem;
	margin-inline: auto;
}

.hero-triangle {
	display: block;
	height: 4vw;
	width: 100%;
	max-width: inherit;
	z-index: 1;
	fill: white;
}

.hero-triangle.bottom {
	transform: translateY(2px);
}

.hero-triangle.top {
	transform: translateY(-2px);
}

.kc-hero > * {
	position: relative;
	z-index: 2;
}

.kc-hero__call-us {
	font-weight: bold;
	align-self: end;
}

@media (max-width: 1024px) {
	.kc-hero__call-us {
		display: none;
	}
}

.kc-hero__call-us a {
	color: #02bdfc !important;
}

.kc-hero__call-us a {
	/* text-decoration: underline; */
	color: white;
}

.kc-hero__call-us a:hover {
	text-decoration: none;
}

.kc-hero__content {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 2rem;
	position: relative;
	padding: 2rem 1rem;
}

@media (min-width: 1024px) {
	.kc-hero__content {
		padding: 6rem 4rem;
	}
}

@media (min-width: 1024px) {
	.hero-headline-image {
		max-width: 48.75rem;
	}
}

.bbb-logo {
	max-width: 3.125rem;
}

.kc-hero__content h3 {
	font-size: 1.5rem;
	font-weight: 400;
	max-width: 42rem;
}

.kc-hero .gform_wrapper {
	background-color: white;
	color: #212529;
	position: relative;
	padding: 1.5rem;
	box-shadow: 0 2px 3px 0 rgba(29, 29, 29, 0.5);
	font-size: 1rem !important;
	width: 100%;
}

@media (min-width: 1024px) {
	.kc-hero .gform_wrapper {
		max-width: 45rem !important;
	}
}

.kc-hero .gform_wrapper {
	margin-bottom: -12rem;
}

@media (max-width: 1024px) {
	.kc-hero .gform_wrapper {
		margin-bottom: 0;
	}
}

.kc-hero .gform_button {
	background-color: #02bdfc;
	border-color: #20c6fe;
	border-top: none;
	border-left: none;
	border-right: none;
	font-size: 1.75rem !important;
	font-weight: 700 !important;
	line-height: 1.5 !important;
	padding: 1.375rem 1.125rem;
	width: 100% !important;
	color: white;
}

.kc-hero .gform_heading {
	display: none !important;
}

/* ------------------------------------------- */
/* ------------ COPIED FORM STYLES ---------- */
/* ----------------------------------------- */

.kc-hero input:focus,
.kc-hero textarea:focus,
.kc-hero select:focus {
	background-color: rgb(255, 255, 255) !important;
	border-color: rgb(128, 189, 255) !important;
	box-shadow: rgba(0, 123, 255, 0.25) 0px 0px 0px 0.2rem !important;
	color: rgb(73, 80, 87) !important;
	outline: 0px !important;
}

.kc-hero .form-control,
.kc-hero .wp-block-search .wp-block-search__input,
.kc-hero input[type="email"],
.kc-heroinput[type="number"],
.kc-hero input[type="password"],
.kc-hero input[type="search"],
.kc-hero input[type="tel"],
.kc-hero input[type="text"],
.kc-hero input[type="url"],
.kc-hero select,
.kc-hero select[multiple],
.kc-hero textarea {
	background-clip: padding-box;
	background-color: #fff;
	border: 1px solid #ced4da;
	border-radius: 0.25rem;
	color: #495057;
	display: block;
	font-size: 1rem;
	font-weight: 400;
	height: calc(1.5em + 0.75rem + 2px);
	line-height: 1.5;
	padding: 0.375rem 0.75rem;
	transition:
		border-color 0.15s ease-in-out,
		box-shadow 0.15s ease-in-out;
	width: 100%;
}

.kc-hero .gfield--type-address {
	display: none !important;
}

.kc-hero .error-message-container {
	border: 1px solid rgb(192, 43, 10);
	background-color: hsl(11, 90%, 95%);
	color: rgb(192, 43, 10);
	font-size: 0.75rem;
	/* font-weight: 700; */
	/* font-style: italic; */
	border-radius: 0.25rem;
	padding: 0.5rem 0.5rem;
	margin-top: 0.5rem;
}

.kc-hero input.invalid {
	border-color: rgb(192, 43, 10) !important;
}

.kc-hero input[type="submit"]:disabled {
	/* background-color: gainsboro !important; */
	/* color: gray !important; */
	opacity: 1 !important;
	/* cursor: not-allowed !important; */
}
